<template>
	<div class="pager">
		<div class='center-title'>{{data.title}}</div>

	</div>
</template>

<script>
import axios from 'axios'
export default {
	data () {
		return {
		  data:{}
		}
	},
	mounted(){
	    axios.get('/api/list').then((res)=>{
	        this.data=res.data
		})
	}
}
</script>
<style>
	body{
		width:100%;
		height:100%;
		margin:0;
		padding:0;
		font-size: 14px;
	    font-family: Helvetica;
	    -webkit-font-smoothing: antialiased;
	}
</style>




